import React, { Fragment } from 'preact';
import { useState, useEffect, useMemo } from 'preact/hooks';
import cn from 'classnames';
import style from './style';

import buildPath from '../../../../data/buildPath';
import calculateSize from './calculateSize';

const ArkMapRoute = ({
	motionMode,
	startPosition, // {row, col}
	endPosition, // {row, col}
	spawnRandomRange, // {x, y}
	spawnOffset, // {x, y}
	checkpoints, // []
	allowDiagonalMove, // Boolean
	visitEveryTileCenter, // Boolean
	visitEveryNodeCenter, // Boolean
	mapData,
	// provided by Ark-Nights
	map_object,
	move_speed = 1,
	route_index,
	timestamp,
	route_active_refs,
	multi_routes_render,
}) => {
	const {
		width: viewBoxWidth,
		height: viewBoxHeight,
	} = calculateSize(mapData);
	const viewBox = `0 0 ${viewBoxWidth} ${viewBoxHeight}`;

	const route_active_data = {
		route_index,
		timestamp,
	};

	const {
		enabled: multi_routes_render_enabled,
		key,
		ref,
		prev_bound,
		next_bound,
	} = multi_routes_render;

	const is_active_route = route_active_refs.route_index_offset >= 0
		&& multi_routes_render_enabled ? (
			route_active_refs[ref] + prev_bound <= route_active_data[key] &&
		route_active_refs[ref] + next_bound >= route_active_data[key]
		) : (
			route_index === route_active_refs.route_index_offset
		);
	const is_highlight_route = route_index === route_active_refs.route_index_offset;

	const route_active_class = `route_${is_active_route ? 'active' : 'inactive'}`;

	const path_data = {
		motionMode,
		viewBoxWidth,
		viewBoxHeight,
		startPosition,
		checkpoints,
		endPosition,
		map_object,
	};
	const {
		path,
		checkpoints_path,
		total_length,
	} = useMemo(() => buildPath(path_data), [route_index]);
	const path_total_time = Math.sqrt(total_length) * 40;
	const path_animate_total_time = Math.sqrt(total_length + 2 * 152) * 40;

	const [route_dasharray, setRouteDasharray] = useState(`0 ${total_length}`);
	const [route_dashoffset, setRouteDashoffset] = useState(0);
	const animate_dasharray = `60 32 60 ${total_length}`;
	const [animate_dashoffset, setAnimateDashoffset] = useState(152);

	useEffect(() => {
		if (is_active_route) {
			setAnimateDashoffset(-total_length);
			setRouteDasharray(`${total_length} 0`);
		} else {
			setAnimateDashoffset(152);
			setRouteDasharray(`0 ${total_length}`);
			if (route_index === route_active_refs.route_index_offset - 1) {
				setRouteDashoffset(-total_length);
			} else {
				setRouteDashoffset(0);
			}
		}
	}, [is_active_route, path]);

	return (
		<Fragment>
			<div
				class={cn(
					style.route,
					style[route_active_class],
					{
						[style.route_highlight]: is_highlight_route,
						[style.route_air]: motionMode === 1,
					}
				)}
			>
				<svg
					width={viewBoxWidth}
					height={viewBoxHeight}
					viewBox={viewBox}
				>
					<path
						d={path}
						style={`
							transition:
								stroke-dasharray ${path_total_time}ms linear 200ms,
								stroke-dashoffset ${path_total_time}ms linear 200ms;
							stroke-dasharray: ${route_dasharray};
							stroke-dashoffset: ${route_dashoffset};
						`}
					/>
					{
						checkpoints_path.length > 0
							&& checkpoints_path.map(p => p({
								style: ({ time_duration, time_offset, r, type }) => `
									stroke-width: 4;
									stroke: ${type === 5 ? 'pink' : 'inherit'};
									transition:
										stroke-dasharray ${is_active_route ? time_duration * 50 : 200}ms linear ${is_active_route ? time_offset * path_total_time + 200 : 0}ms,
										fill 200ms linear ${is_active_route ? time_offset * path_total_time + 200 : 0}ms;
									stroke-dasharray: ${is_active_route ? `${r * 6.284} 0` : `0 ${r * 6.284}`};
									fill: ${is_active_route ? 'rgba(120, 120, 120, 1)' : 'rgba(120, 120, 120, 0)'};
								`,
							}))
					}
					{
						checkpoints_path.length > 0
							&& checkpoints_path.map(p => p({
								style: ({ time_offset }) => `
									stroke: none;
									transition:
										fill 200ms linear ${is_active_route ? time_offset * path_total_time + 200 : 0}ms;
									fill: ${is_active_route ? 'rgba(240, 240, 240, 1)' : 'rgba(240, 240, 240, 0)'};
								`,
								get_text: true,
							}))
					}
				</svg>
			</div>
			<div
				class={cn(
					style.route_shadow,
					style[route_active_class],
					{
						[style.route_air_shadow]: motionMode === 1,
					}
				)}
			>
				<svg
					width={viewBoxWidth}
					height={viewBoxHeight}
					viewBox={viewBox}
					style={`
						transition:
							stroke-dasharray ${path_total_time}ms linear 200ms,
							stroke-dashoffset ${path_total_time}ms linear 200ms;
						stroke-dasharray: ${route_dasharray};
						stroke-dashoffset: ${route_dashoffset};
					`}
				>
					<path d={path} />
				</svg>
			</div>
			{
				is_active_route && (
					<Fragment>
						<div
							class={cn(
								style.route_animate,
								style[route_active_class],
								{
									[style.route_animate_air]: motionMode === 1,
								}
							)}
						>
							<svg
								width={viewBoxWidth}
								height={viewBoxHeight}
								viewBox={viewBox}
								style={`
									transition:
										stroke-dashoffset ${path_animate_total_time}ms linear 200ms;
									stroke-dasharray: ${animate_dasharray};
									stroke-dashoffset: ${animate_dashoffset};
								`}
							>
								<path d={path} />
							</svg>
						</div>
					</Fragment>
				)
			}
		</Fragment>
	);
};

export {
	style,
};

export default ArkMapRoute;
