import React from 'preact';
import style from './style';
import available_list from './list';

const ArkEnemyIcon = ({
	enemy_key,
	size,
	enable_hover_detail,
}) => {
	const url = available_list.includes(enemy_key)
		? `../../assets/enemy_icons/${enemy_key}.png`
		: `../../assets/404.png`;

	if (!available_list.includes(enemy_key)) {
		console.log(`Cannot find ${enemy_key}`);
	}

	return (
		<div class={style.wrapper}>
			<img
				class={style.enemy_icon} src={url} alt={enemy_key}
				style={{ width: size, height: size }}
			/>
			{
				// TODO: hover key
			}
		</div>
	);
};

export default ArkEnemyIcon;
