import { useReducer } from 'preact/hooks';
import getRange from '../data/getRange';

export const STORAGE_KEY_SQUAD = 'Towa_ArkMap_Squad';
const getStorageKey = (level_id) => level_id && `Towa_ArkMap_Plan_${level_id}`;

const save = (state) => {
	const {
		level_id,
		squad,
		...rest
	} = state;
	const data = {
		rest,
	};

	window.localStorage.setItem(STORAGE_KEY_SQUAD, JSON.stringify(squad));

	if (level_id) {
		global[getStorageKey(level_id)] = data;
	}
};

const default_state = {
	isInitialized: false,
	squad: {},
	plan: [],
	// plan: [
	// 	{
	// 		col: 2,
	// 		direction: 1,
	// 		heightType: 1,
	// 		op: 0,
	// 		operator_key: 'char_103_angel',
	// 		route_index_offset: -1,
	// 		row: 2,
	// 	},
	// ],
	mapData: null,
	operator_default_range_tiles: [],
	operator_active_range_tiles: [],
	map_operators: [],
	operator_deployed: {},
};

const reducer = (state, action) => {
	let newState = state;
	switch (action.type) {
		case 'data.load': {
			let squad = {};
			try {
				const json = window.localStorage.getItem(STORAGE_KEY_SQUAD);
				if (json) {
					const loaded = JSON.parse(json);
					if (loaded) {
						squad = loaded;
					}

					return {
						...default_state,
						squad,
					};
				}
			} catch (err) {
				window.localStorage.removeItem(STORAGE_KEY_SQUAD);
			}

			// const json = action.payload || global[getStorageKey(action.payload && action.payload.level_id)];
			// try {
			// 	if (json) {
			// 		const loaded = JSON.parse(json);
			// 		if (action.payload) {
			// 			save(loaded);
			// 		}
			// 		return {
			// 			squad,
			// 			...loaded,
			// 		};
			// 	}
			// } catch (err) {
			// 	if (action.payload) {
			// 		throw err;
			// 	}
			// 	// window.localStorage.removeItem(STORAGE_KEY);
			// 	return {
			// 		squad,
			// 		...default_state,
			// 	};
			// }
			break;
		}
		case 'data.initialize': {
			const mapData = action.payload;
			const tiles = mapData.tiles.map((tile, tile_index) => ({
				...tile,
				tile_index,
			}));
			const operator_default_range_tiles = Array(tiles.length).fill(false);
			const map_operators = Array(tiles.length).fill(false);

			newState = {
				...state,
				isInitialized: true,
				mapData,
				tiles,
				operator_default_range_tiles,
				map_operators,
			};
			break;
		}
		case 'squad.toggleOperator': {
			const {
				operator_key,
				...rest
			} = action.payload;

			const newSquad = {
				...state.squad,
			};
			if (state.squad[operator_key]) {
				delete newSquad[operator_key];
			} else {
				newSquad[operator_key] = {
					...rest,
				};
			}

			newState = {
				...state,
				squad: newSquad,
			};
			break;
		}
		case 'squad.updateOperator': {
			const {
				operator_key,
				...updates
			} = action.payload;
			const current_data = state.squad[operator_key];
			if (!current_data) {
				return;
			}

			const newSquad = {
				...state.squad,
			};
			newSquad[operator_key] = {
				...current_data,
				...updates,
			};

			newState = {
				...state,
				squad: newSquad,
			};
			break;
		}
		case 'plan.deployOperator': {
			const deployment_data = action.payload;

			// operator already deployed
			if (state.map_operators.some(tile => tile && tile.char_key.startsWith(deployment_data.operator_key))) {
				break;
			}

			// The tile is occupied;
			const tile_index = deployment_data.row * state.mapData.width + deployment_data.col;
			if (state.map_operators[tile_index]) {
				break;
			}

			const newPlan = [...state.plan];

			newPlan.push({
				...deployment_data,
				op: 0,
			});

			newState = {
				...state,
				plan: newPlan,
			};
			break;
		}
		case 'plan.withdrawOperator': {
			const deployment_data = action.payload;

			// operator not yet deployed
			if (!state.map_operators.some(tile => tile && tile.char_key.startsWith(deployment_data.operator_key))) {
				break;
			}

			const newPlan = [...state.plan];

			newPlan.push({
				...deployment_data,
				op: 1,
			});

			newState = {
				...state,
				plan: newPlan,
			};
			break;
		}
		default:
			throw new Error(`Undefined action type: ${action.type}`);
	}


	const {
		isInitialized,
		mapData,
		plan,
		squad,
	} = newState;

	if (isInitialized) {
		const {
			width,
			height,
		} = mapData;

		// Reduce the map_operators
		const new_map_operators = [];
		const new_operator_deployed = {};

		plan.forEach(({
			op,
			operator_key,
			row,
			col,
			direction,
			route_index_offset,
		}) => {
			const tile_index = row * width + col;
			if (op === 0) {
				new_map_operators[tile_index] = {
					row,
					col,
					operator_key,
					direction,
					char_key: squad[operator_key].char_key,
					skill_icon: squad[operator_key].skill_icon,
				};
				new_operator_deployed[operator_key] = true;
			}
			if (op === 1) {
				new_map_operators[tile_index] = null;
				new_operator_deployed[operator_key] = false;
			}
		});

		newState.map_operators = new_map_operators;
		newState.operator_deployed = new_operator_deployed;

		// Calculate the attack range
		const new_operator_default_range_tiles = new Array(width * height);
		const new_operator_active_range_tiles = new Array(width * height);
		new_map_operators.filter(Boolean).forEach(({
			row,
			col,
			direction,
			operator_key,
		}) => {
			const {
				attack_range,
			} = squad[operator_key];

			const range = getRange({ row, col, direction, rangeId: attack_range });
			range.forEach(({ row: v, col: u }) => {
				if (v < 0 || u < 0 || v >= height || u >= width) {
					return;
				}
				const tile_index = v * width + u;
				new_operator_default_range_tiles[tile_index] = new_operator_default_range_tiles[tile_index] || {};
				new_operator_default_range_tiles[tile_index][operator_key] = {
					is_healing: squad[operator_key].default_is_healing,
				};
				new_operator_active_range_tiles[tile_index] = new_operator_active_range_tiles[tile_index] || {};
				new_operator_active_range_tiles[tile_index][operator_key] = {
					is_healing: squad[operator_key].default_is_healing,
				};
			});
		});

		newState.operator_default_range_tiles = new_operator_default_range_tiles;
		newState.operator_active_range_tiles = new_operator_active_range_tiles;
	}

	console.log(newState);

	if (action.type !== 'data.load') {
		save(newState);
	}
	return newState;
};

const useMapPlan = () => {
	const [state, dispatch] = useReducer(reducer, default_state);

	const load = (json) => dispatch({
		type: 'data.load',
		payload: json,
	});

	const initialize = (mapData) => dispatch({
		type: 'data.initialize',
		payload: mapData,
	});

	const toggleSquadOperator = (operator_data) => dispatch({
		type: 'squad.toggleOperator',
		payload: operator_data,
	});

	const updateSquadOperator = (operator_data) => dispatch({
		type: 'squad.updateOperator',
		payload: operator_data,
	});

	const deployOperator = (data) => dispatch({
		type: 'plan.deployOperator',
		payload: data,
	});

	const withdrawOperator = (data) => dispatch({
		type: 'plan.withdrawOperator',
		payload: data,
	});

	return {
		state,
		dispatch,
		load,
		initialize,
		deployOperator,
		withdrawOperator,
		toggleSquadOperator,
		updateSquadOperator,
	};
};

export default useMapPlan;
