import { RANGES } from '../../../models/Ranges';

const getRange = ({
	row,
	col,
	direction,
	rangeId,
}) => {
	const range = [];
	const range_data = RANGES[rangeId];
	if (!range_data)
		return range;

	if (direction === 0) {
		range_data.grids.forEach(({ row: v, col: u }) => {
			range.push({
				row: row + u,
				col: col - v,
			});
		});
	}
	if (direction === 1) {
		range_data.grids.forEach(({ row: v, col: u }) => {
			range.push({
				row: row + v,
				col: col + u,
			});
		});
	}
	if (direction === 2) {
		range_data.grids.forEach(({ row: v, col: u }) => {
			range.push({
				row: row - u,
				col: col + v,
			});
		});
	}
	if (direction === 3) {
		range_data.grids.forEach(({ row: v, col: u }) => {
			range.push({
				row: row - v,
				col: col - u,
			});
		});
	}

	return range;
};

export default getRange;
