import React from 'preact';
import ArkRow from '../../../../../components/row';

import ArkCell from '../../../../../components/cell';
import ArkEnemyIcon from '../../../../../components/enemyIcon';

const EnemyIconCell = (enemy_key, size) => (props) => (
	<ArkCell>
		<ArkEnemyIcon
			{...props}
			enemy_key={enemy_key}
			size={size}
		/>
	</ArkCell>
);

const ArkWaveFragmentAction = ({
	// 0: Normal spwan
	// 1: No enemy (For route preview)
	// 2: TUT
	// 5: Show enemy info (Top right ribbon)
	// 6: Operator
	actionType, // 0
	managedByScheduler, // true
	enemyKey, // "enemy_1000_gopro"
	count, // 1
	preDelay, // 3.0
	interval, // 1.0
	routeIndex, // 32
	blockFragment, // false
	autoPreviewRoute, // true -> show route preview
	isUnharmfulAndAlwaysCountAsKilled,
	waveIndex,
	fragmentIndex,
	actionIndex,
	timestamp,
}) => (
	<ArkRow
		cells={[
			{ content: waveIndex },
			{ content: fragmentIndex },
			{ content: actionIndex },
			EnemyIconCell(enemyKey, '48px'),
			{ content: routeIndex },
			{ content: timestamp },
			{ content: preDelay },
			{ content: count },
		]}
		fullheight
	/>
);

export default ArkWaveFragmentAction;
