const getWavesRoutes = ({
	waves,
	wave_interval_fallback = 5,
}) => {
	// console.log(waves);
	// Group by wave
	let total_count = 0;
	let wave_delay_offset = 0;
	const all_routes = waves.map((wave, wave_index) => {
		const wave_routes = [];
		// console.log(wave);
		// console.log(`Name: ${wave.name}`);
		let delay = 0;
		let wave_count = 0;
		delay += wave.preDelay;
		wave.fragments.forEach((fragment, fragment_index) => {
			// console.log(fragment);
			// console.log(`Name: ${fragment.name}`);
			delay += fragment.preDelay;
			let fragment_delay_anchor = delay;
			fragment.actions
				.sort((a, b) => a.preDelay - b.preDelay)
				.forEach((action, action_index) => {
					// console.log(`w${wave_index}-f${fragment_index}-a${action_index}`);
					// console.log(action.routeIndex);
					// console.log(action);
					// console.log('------------');
					if (![0, 1, 2, 5, 6].includes(action.actionType)) {
						console.error('!!!!');
						console.error(action);
					}

					if ([1, 2, 5, 6].includes(action.actionType)) {
						return;
					}

					delay = fragment_delay_anchor + action.preDelay;
					wave_routes.push({
						routeIndex: action.routeIndex,
						wave_index,
						fragment_index,
						action_index,
						count: action.count,
						enemy_key: action.key,
						interval: action.interval,
						wave_timestamp: delay,
						timestamp: delay + wave_delay_offset,
						wait: false,
						total_count: total_count + 1,
						enemy_index_range: action.count === 1
							? `${total_count + action.count}`
							: `${total_count + 1} ~ ${total_count + action.count}`,
					});

					delay += (action.count - 1) * action.interval;
					// console.log(`${action.key}: ${delay}`);
					// interval

					total_count += action.count;
					wave_count += action.count;
				});
		});
		delay += wave.postDelay;
		wave_delay_offset += delay + wave_interval_fallback;
		if (wave_routes.length > 0) {
			wave_routes[wave_routes.length - 1].wait = wave.maxTimeWaitingForNextWave;
		}
		// console.log('wave_count');
		// console.log(wave_count);
		return wave_routes;
	});

	// console.log('total_count');
	// console.log(total_count);

	// console.log('all_routes');
	// console.log(all_routes);

	return {
		all_routes,
		all_routes_flatten: [].concat(...all_routes),
		total_count,
	};
};

export default getWavesRoutes;
