import React from 'preact';
import cn from 'classnames';
import style from './style';

const ArkTileOverlay = ({
	type,
	content,
	options,
	zIndex,
}) => (
	<span
		class={cn(
			style.tile_overlay,
			style[type],
			{
				[style.tile_block_overlay]: options.content_on_top,
				[style.content_on_top]: options.content_on_top,
				[style.content_on_box]: options.content_on_box,
				[style.content_fade_out_perspective]: options.content_fade_out_perspective,
				[style.content_fade_out_flat]: options.content_fade_out_flat,
				[style.range_healing]: options.is_healing,
				[style.range_active]: options.range_active,
			}
		)}
		style={{
			zIndex,
		}}
	>{content}</span>
);

export default ArkTileOverlay;
