import React from 'preact';
import { useEffect, useState } from 'preact/hooks';
import cn from 'classnames';
import style from './style';

import ArkCharIcon from './../../../../../../components/charIcon';
import ArkProfessionIcon from './../../../../../../components/professionIcon';
import ArkEliteIcon from './../../../../../../components/eliteIcon';
import ArkSkillIcon from './../../../../../../components/skillIcon';
import ArkRangePreview from '../../../../../../components/rangePreview';
import ArkSquashText from './../../../../../../components/text/squash';
import { ArkError } from '../../../../../../components/helpBlock';

const ArkOperatorRow = ({
	operator_key,
	name,
	appellation,
	profession,
	deploy_flag,
	rarity,
	skills,
	phases,
	skin_icons,
	toggleSquadOperator,
	updateSquadOperator,
	squad_inst_data,
}) => {
	if (!operator_key) {
		return (
			<div class={style.wrapper}>
				<ArkError message={'Invalid data'} />
			</div>
		);
	}

	const [evolve_phase, setEvolvePhase] = useState(squad_inst_data && squad_inst_data.evolve_phase || 0);
	const [active_skill_index, setActiveSkillIndex] = useState(squad_inst_data && squad_inst_data.active_skill_index || 0);

	useEffect(() => {
		if (squad_inst_data) {
			if (evolve_phase !== squad_inst_data.evolve_phase) {
				setEvolvePhase(squad_inst_data.evolve_phase);
			}
			if (active_skill_index !== squad_inst_data.active_skill_index) {
				setActiveSkillIndex(squad_inst_data.active_skill_index);
			}
		}
	}, [squad_inst_data]);

	return (
		<div class={style.wrapper}>
			<div
				class={cn(
					style.operator_card,
					{
						[style.operator_card_active]: Boolean(squad_inst_data),
					}
				)}
				onClick={e => {
					toggleSquadOperator({
						operator_key,
						rarity,
						char_key: skin_icons[evolve_phase],
						deploy_flag,
						evolve_phase,
						active_skill_index,
						attack_range: phases[evolve_phase].rangeId,
						default_is_healing: profession === 'MEDIC',
						skill_range: skills.length > 0 && skills[active_skill_index].levels[0].rangeId,
						skill_icon: skills.length > 0 && `skill_icon_${skills[active_skill_index].iconId}`,
					});
				}}
			>
				<div class={style.operator_icon}>
					<ArkCharIcon char_key={skin_icons[evolve_phase] || operator_key} rarity={rarity} size="64px" />
					<div class={style.operator_data_rarity}>
						{
							Array(rarity + 1).fill().map(e => (
								<span class={style.operator_data_rarity_star}>★</span>
							))
						}
					</div>
					<div style={{ marginTop: '4px' }}>
						<ArkProfessionIcon profession={`icon_profession_${profession.toLowerCase()}_large`} size="40px" />
					</div>
				</div>
				<div class={style.operator_data}>
					<div
						class={cn(
							style.operator_data_row,
							style.operator_data_name_row,
						)}
					>
						<ArkSquashText text={name.split('').map(c => `_${c}`).join('')} fontSize="16px" />
						<span style={{ marginLeft: '6px', fontSize: '16px', fontFamily: 'serif', fontWeight: 'bold' }}>{` ${appellation}`}</span>
					</div>
					<div
						class={cn(
							style.operator_data_row,
							style.operator_data_elite_row,
						)}
					>
						{
							Array(phases.length).fill().map((_, phase_index) => (
								<div
									class={cn(
										style.operator_data_evolve_phase,
										{
											[style.operator_data_evolve_phase_active]: phase_index === evolve_phase,
										}
									)}
									onClick={e => {
										e.preventDefault();
										e.stopPropagation();
										setEvolvePhase(phase_index);
										if (squad_inst_data) {
											updateSquadOperator({
												operator_key,
												char_key: skin_icons[phase_index],
												evolve_phase: phase_index,
												attack_range: phases[phase_index].rangeId,
											});
										}
									}}
								>
									<ArkEliteIcon evolve_phase={`elite_${phase_index}_large`} size="36px" />
								</div>
							))
						}
					</div>
					<div
						class={cn(
							style.operator_data_row,
							style.operator_data_skill_row,
						)}
					>
						{
							skills.map((skill, skill_index) => (
								<div
									class={cn(
										style.operator_data_skill,
										{
											[style.operator_data_skill_active]: skill_index === active_skill_index,
										}
									)}
									onClick={e => {
										e.preventDefault();
										e.stopPropagation();
										setActiveSkillIndex(skill_index);
										if (squad_inst_data) {
											updateSquadOperator({
												operator_key,
												active_skill_index: skill_index,
												skill_range: skill.rangeId,
												skill_icon: `skill_icon_${skill.iconId}`,
											});
										}
									}}
								>
									<ArkSkillIcon skill_key={`skill_icon_${skill.iconId}`} size="36px" />
								</div>
							))
						}
					</div>
					<hr style={{ margin: '0 0 6px', width: '100%' }} />
					<div
						class={cn(
							style.operator_data_row,
							style.operator_data_range_row
						)}
					>
						<div class={style.operator_data_range}>
							<ArkRangePreview range_key={phases[evolve_phase].rangeId} />
						</div>
						{
							skills.length > 0 && skills[active_skill_index].levels[0].rangeId
								&& skills[active_skill_index].levels[0].rangeId !== phases[evolve_phase].rangeId
								&& (
									<div class={style.operator_data_range}>
										<ArkRangePreview range_key={skills[active_skill_index].levels[0].rangeId} skill_range />
									</div>
								)
						}
					</div>
				</div>
			</div>
		</div>
	);
};

export default ArkOperatorRow;
