import React from 'preact';
import { useState } from 'preact/hooks';
import Fuse from 'fuse.js';
import style from './style';

import { OPERATORS } from '../../../../models/Operators';

const options = {
	shouldSort: true,
	threshold: 0.8,
	location: 0,
	distance: 100,
	maxPatternLength: 16,
	minMatchCharLength: 1,
	keys: [{
		name: 'name',
		weight: 0.4,
	}, {
		name: 'pinyin',
		weight: 0.3,
	}, {
		name: 'abbr',
		weight: 0.1,
	}, {
		name: 'appellation',
		weight: 0.2,
	}],
};
const operator_entries = Object.entries(OPERATORS)
	.sort((a, b) => b[1].rarity - a[1].rarity);
const fuse = new Fuse(operator_entries.map(([key, value]) => value), options);
const default_filter = {};
Object.keys(OPERATORS).forEach(key => {
	default_filter[key] = -1;
});

import ArkOperatorItem from './components/operator';

const ArkOperatorsList = ({
	ir,
	squad,
	toggleSquadOperator,
	updateSquadOperator,
}) => {
	const [query, setQuery_raw] = useState(null);
	const [filter_list, setFilterList] = useState(default_filter);

	let debounce = -1;
	const setQuery = (input) => {
		if (debounce) {
			clearTimeout(debounce);
		}
		debounce = setTimeout(() => {
			const new_filter = { ...default_filter };
			const result = fuse.search(input);
			result.forEach(({ operator_key }, order) => {
				new_filter[operator_key] = order + 1;
			});

			setQuery_raw(input);
			setFilterList(() => new_filter);
		}, 200);
	};

	return (
		<div class={style.wrapper}>
			<div class={style.operator_query}>
				<input
					type="text"
					placeholder={ir('side_panel-operators-search_placeholder', 'Search here...')}
					value={query}
					onInput={e => setQuery(e.target.value)}
				/>
			</div>
			<div class={style.operator_list_scroll}>
				<div class={style.operator_list}>
					{
						operator_entries
							.filter(
								([operator_key]) => !query || (
									filter_list[operator_key] > 0
										&& filter_list[operator_key] <= 12
								)
							)
							.filter((_, index) => index < 12)
							.map(([operator_key, operator_data]) => (
								<div
									class={style.operator_row}
									key={operator_key}
									style={{ order: query && filter_list[operator_key] }}
								>
									<ArkOperatorItem
										squad_inst_data={squad[operator_key]}
										toggleSquadOperator={toggleSquadOperator}
										updateSquadOperator={updateSquadOperator}
										operator_key={operator_key}
										{...operator_data}
									/>
								</div>
							))
					}
				</div>
			</div>
			<div class={style.wrapper_cover} />
		</div>
	);
};

export default ArkOperatorsList;
