import React from 'preact';
import cn from 'classnames';
import style from './style';

import ArkMapTile, { TileDict } from './';

const TileLegendOrder = {
	tile_forbidden: 1,
	tile_floor: 2,
	tile_road: 3,
	tile_wall: 4,
	tile_fence: 5,
	tile_hole: 6,
	tile_empty: 7,
	tile_start: 8,
	tile_flystart: 9,
	tile_end: 10,
	tile_telin: 11,
	tile_telout: 12,
	tile_grass: 13,
	tile_healing: 14,
	tile_defup: 15,
	tile_bigforce: 16,
	tile_gazebo: 17,
	tile_infection: 18,
	tile_volcano: 19,
	tile_corrosion: 20,
	tile_rcm_operator: 21,
	tile_rcm_crate: 22,
	tile_crate: 23,
};

const ArkMapTileLegend = ({
	ir,
	tileKey,
}) => (
	<div
		class={style.tile_legend}
		style={{
			order: TileLegendOrder[tileKey] || -1,
		}}
	>
		<ArkMapTile tileKey={tileKey} small />
		<span
			class={cn(
				{
					[style.tile_legend_long_text]: TileDict[tileKey].legend.length >= 11,
				}
			)}
		>{ir(`map-map-legends-${tileKey}`, TileDict[tileKey].legend)}</span>
	</div>
);

export default ArkMapTileLegend;
