import parseCheckpoint from './parseCheckpoint';

const DirectionDelta = {
	0: { row: 1, col: 0 },
	1: { row: 1, col: 1 },
	2: { row: 0, col: 1 },
	3: { row: -1, col: 1 },
	4: { row: -1, col: 0 },
	5: { row: -1, col: -1 },
	6: { row: 0, col: -1 },
	7: { row: 1, col: -1 },
};

class ArkMapTile {
	constructor({
		row,
		col,
		tileKey,
		heightType,
		buildableType,
		passableMask,
		map,
	}) {
		this.map = map;
		this.row = row;
		this.col = col;

		this.tileKey = tileKey;

		// 0 : Ground
		// 1 : Wall
		this.heightType = heightType;

		// [Flags]
		// 1 << 0: Ground
		// 1 << 1: Air
		this.buildableType = buildableType;

		// [Flags]
		// 1 << 0: Ground
		// 1 << 1: Air
		this.passableMask = passableMask;

		this.visited = false;
		this.pointer = null;
	}

	getTile(direction) {
		if (!this.map) {
			console.warn('Invalid map ref');
			return null;
		}

		const delta = DirectionDelta[direction];
		return this.map.getTileAt({
			row: this.row + delta.row,
			col: this.col + delta.col,
		});
	}

	getAvailableNeib({
		allow_diagonal,
		motionMode = 0,
	}) {
		const neib = allow_diagonal
			? Array(8).fill().map((_, direction) => this.getTile(direction))
			: Array(4).fill().map((_, i) => i * 2).map(direction => this.getTile(direction));

		return neib
			.filter(Boolean)
			.filter(tile => Boolean(tile.passableMask & (0x1 << motionMode)))
			.filter(tile => tile.tileKey !== 'tile_hole' || motionMode !== 0);
	}

	pointTo(tile, dist) {
		if (!this.pointer || this.pointer && dist < this.pointer.dist) {
			this.pointer = {
				tile,
				dist,
			};
		}
	}

	connectPointers() {
		const path = [{
			row: this.row,
			col: this.col,
		}];
		let visiting = this;
		while (visiting.pointer !== null) {
			path.unshift({
				row: visiting.pointer.tile.row,
				col: visiting.pointer.tile.col,
			});
			visiting = visiting.pointer.tile;
		}
		return path;
	}
}

class ArkMapObject {
	constructor({
		map,
		tiles,
		width,
		height,
	}) {
		this.initialized = false;

		this.tiles_ref = tiles.slice();
		this.width = width;
		this.height = height;

		// Build Array;
		this.tiles = Array(height).fill().map(
			(_, v) => Array(width).fill().map(
				(_, u) => new ArkMapTile({
					...this.tiles_ref[v * width + u],
					row: v,
					col: u,
					map: this,
				})
			)
		);

		this.initialized = true;
	}

	getTileAt({ row, col }) {
		if (row < 0 || col < 0 || row >= this.height || col >= this.width) {
			return null;
		}
		return this.tiles[row][col];
	}

	placeBoxAt({ row, col }) {
		this.tiles[row][col].passableMask &= 0x10;
	}

	// fn({tile, row, col})
	forEachTile(fn) {
		Array(this.height).fill().forEach(
			(_, v) => Array(this.width).fill().forEach(
				(_, u) => fn({
					tile: this.tiles[v][u],
					row: v,
					col: u,
				})
			)
		);
	}

	resetAllTile() {
		this.forEachTile(({ tile }) => {
			tile.visited = false;
			tile.pointer = null;
		});
	}

	findPathBetween(
		from,
		to,
		{
			motionMode = 0,
			allow_diagonal = false,
			point_data,
		}
	) {
		if (
			from.row < 0 ||
			from.col < 0 ||
			from.row >= this.height ||
			from.col >= this.width ||
			to.row < 0 ||
			to.col < 0 ||
			to.row >= this.height ||
			to.col >= this.width
		) {
			return null;
		}

		let path = null;
		const to_visit = [];
		to_visit.push({
			dist: 0,
			tile: this.getTileAt(from),
		});
		while (to_visit.length > 0) {
			// console.log(
			// 	to_visit.map(
			// 		({ dist, tile: { row, col } }) => `${row},${col}:${dist}`
			// 	).join(' ')
			// );
			const { dist, tile: tile_visiting } = to_visit.shift();
			tile_visiting.visited = true;

			if (tile_visiting.row === to.row && tile_visiting.col === to.col) {
				 path = tile_visiting.connectPointers();
				 break;
			}

			const neib = tile_visiting
				.getAvailableNeib({
					motionMode,
					allow_diagonal,
				})
				.filter(Boolean)
				.filter(({ visited }) => !visited);

			neib.forEach(tile => {
				tile.pointTo(tile_visiting, dist + 1);
				if (to_visit.length === 0 || to_visit[to_visit.length - 1].dist < dist) {
					to_visit.push({
						dist: dist + 1,
						tile,
					});
					return;
				}
				let index = to_visit.length;
				while (index > 0 && to_visit[index - 1].dist > dist) {
					index--;
				}
				to_visit.splice(index, 0, {
					dist: dist + 1,
					tile,
				});
			});
		}

		this.resetAllTile();
		path[0].reachOffset = from.reachOffset;
		path[path.length - 1].reachOffset = to.reachOffset;
		if ([1, 3, 5].includes(to.type)) {
			path[path.length - 1] = {
				...path[path.length - 1],
				...point_data,
			};
		}
		return path;
	}

	findPath({
		startPosition,
		checkpoints,
		endPosition,
		motionMode,
	}) {
		let last_point = null;
		const points = [
			startPosition,
			...checkpoints.map(parseCheckpoint),
			endPosition,
		].map(point => {
			// dependent type, map to previous point type
			if ([1, 3, 5].includes(point.type)) {
				return {
					...point,
					row: last_point.row,
					col: last_point.col,
					reachOffset: last_point.reachOffset,
				};
			}

			last_point = point;
			return point;
		});

		if (points.some(
			point => !this.checkTilePassable(this.getTileAt(point), { motionMode }))
		) {
			// Not all points can pass, fallback to default
			return points;
		}

		const routing = [];
		for (let i = 0; i < points.length - 1; ++i) {
			if ([5, 6].includes(points[i].type)) {
				routing.push({
					...points[i],
				});
			}
			if ([5, 6].includes(points[i + 1].type)) {
				continue;
			}

			const segment = this.findPathBetween(points[i], points[i + 1], {
				motionMode,
				allow_diagonal: false,
				point_data: points[i + 1],
			});

			const merged_segment = this.merge(segment, {
				motionMode,
			});

			// Remove the duplicated point
			if (i !== 0) {
				merged_segment.unshift();
			}

			routing.push(...merged_segment);
		}
		return routing;
	}

	checkTilePassable(tile, {
		motionMode,
	}) {
		return (tile.passableMask & 1 << motionMode)
			&& (motionMode !== 0 || !['tile_hole', 'tile_empty'].includes(tile.tileKey));
	}

	checkArea({ from, to, motionMode }) {
		const area_w = Math.abs(from.col - to.col) + 1;
		const area_h = Math.abs(from.row - to.row) + 1;
		const area_x = Math.min(from.col, to.col);
		const area_y = Math.min(from.row, to.row);
		// Check the react area specified by from to
		const result = Array(area_h).fill().every((_, v) =>
			Array(area_w).fill().every((_, u) =>
				this.checkTilePassable(this.tiles[v + area_y][u + area_x], {
					motionMode,
				})
			)
		);

		return result;
	}

	merge(path, {
		motionMode,
	}) {
		if (path.length <= 2) {
			return path;
		}
		const merged_path = [];
		let anchor_start = path[0];
		let anchor_end = null;
		path.forEach(point => {
			if (merged_path.length === 0) {
				merged_path.push(point);
				return;
			}
			// Check area between anchor_start and anchor_end
			if (this.checkArea({
				from: anchor_start,
				to: point,
				motionMode,
			})) {
				anchor_end = point;
				return;
			}
			merged_path.push(anchor_end);
			anchor_start = anchor_end;
			if (this.checkArea({
				from: anchor_start,
				to: point,
				motionMode,
			})) {
				anchor_end = point;
				return;
			}
		});
		merged_path.push(path[path.length - 1]);

		return merged_path;
	}
}

export default ArkMapObject;
