import React from 'preact';
import cn from 'classnames';
import style from './style';
import available_list from './list';

const ArkCharIcon = ({
	char_key,
	rarity,
	size,
	enable_hover_detail,
}) => {
	const url = available_list.includes(char_key)
		? `../../assets/char_icons/${char_key}.png`
		: `../../assets/404.png`;

	if (!available_list.includes(char_key)) {
		console.log(`Cannot find ${char_key}`);
	}

	return (
		<div class={style.wrapper}>
			<img
				class={style.char_icon}
				src={url}
				alt={char_key}
				style={{ width: size, height: size }}
			/>
			<div
				class={cn(
					style.char_icon_border,
					{
						[style.char_icon_withbar]: rarity !== undefined,
					}
				)}
			/>
			{
				rarity !== undefined && (
					<span
						class={cn(
							style.char_icon_rarity_bar,
							[style[`T${rarity + 1}`]]
						)}
					/>
				)
			}
			{
				rarity === 5 && (
					<div
						class={cn(
							style.char_icon_rarity_cover,
							[style[`T${rarity + 1}`]]
						)}
					/>
				)
			}
			{
				// TODO: hover key
			}
		</div>
	);
};

export default ArkCharIcon;
