import React from 'preact';
import style from '../style';

import ArkRow from '../../../../../components/row';
import ArkCell from '../../../../../components/cell';

import getCoordinate from '../../../data/getCoordinate';

const ArkRouteRow = ({
	mapData,
	motionMode,
	startPosition, // {row, col}
	endPosition, // {row, col}
	spawnRandomRange, // {x, y}
	spawnOffset, // {x, y}
	checkpoints, // []
	allowDiagonalMove, // Boolean
	visitEveryTileCenter, // Boolean
	visitEveryNodeCenter, // Boolean
	routeIndex = 0,
	routeIndexOffset,
}) => (
	<ArkRow
		cells={[
			{ content: routeIndex },
			{ content: motionMode },
			{ content: getCoordinate(startPosition) },
			{ content: getCoordinate(endPosition) },
		]}
	/>
);

export default ArkRouteRow;
