import React from 'preact';
import cn from 'classnames';
import style from './style';

import ArkCharIcon from '../../../../../../components/charIcon';

const ArkTileOperator = ({
	char_key,
	direction,
}) => (
	<div
		class={cn(
			style.wrapper,
			style[`direction_${direction}`]
		)}
	>
		<div class={style.floating} >
			<ArkCharIcon char_key={char_key} size="40px" />
		</div>
		<span class={style.direction} />
		<span class={style.shadow} />
	</div>
);

export default ArkTileOperator;
