import React from 'preact';
import { useState, useEffect, useRef } from 'preact/hooks';
import style from './style';

import ArkSkillIcon from '../../../../../../components/skillIcon';

const HandleStatus = {
	IDLE: 'IDLE',
	PRESSED: 'PRESSED',
	MOVING: 'MOVING',
	CANCELED: 'CANCELED',
	TRIGGERED: 'TRIGGERED',
};

const handle_default_pos = { x: 0, y: 0 };

const limit = ({ x, y }) => {
	if (x === 0 && y === 0) {
		return { x, y };
	}
	const r = Math.hypot(x, y);
	const nx = x / r;
	const ny = y / r;
	const p = Math.min(Math.max((r / 36) ** 1.2, 0), 1);
	return {
		x: nx * p * 84,
		y: ny * p * 84,
	};
};

const rotate = ({ x, y }) => ({
	x: (x * 0.707 + y * 0.707) / 1.414,
	y: (-x * 0.707 + y * 0.707) / 1.414,
});

const getDirection = ({ x ,y }) => (Math.round(2 * Math.atan2(y, x) / Math.PI) + 5) % 4;

const ArkOperatorDeployment = ({
	env,
	squad,
	deployment_pos,
	deployment_coord,
	options_pos,
	options_coord,
	options_data,
	tile_buildable_type,
	operator_key,
	setDeployment,
	cancelDeployment,
	confirmDeployment,
	setDeploymentOptions,
	cancelDeploymentOptions,
	withdrawOperator,
}) => {
	const wrapperRef = useRef(null);
	const maskingRef = useRef(null);
	const handleFrameRef = useRef(null);
	const handleFrameMaskingRef = useRef(null);
	const handleRef = useRef(null);

	const [masking_size, setMaskingSize] = useState({ width: 0, height: 0 });
	useEffect(() => {
		setMaskingSize({
			width: wrapperRef.current.clientWidth,
			height: wrapperRef.current.clientHeight,
		});
		if (deployment_pos && operator_key && squad[operator_key]) {
			const deployable = (squad[operator_key].deploy_flag & tile_buildable_type);
			if (deployable) {
				maskingRef.current.style.transform = `translateZ(${deployment_pos.z}px)`;
				moveHandleFrame(deployment_pos);
				moveHandle(handle_default_pos);
				showMasking();
				showHandleFrame();
				showHandle();
				return;
			}
			cancelDeployment();
		}

		if (options_pos) {
			maskingRef.current.style.transform = `translateZ(${options_pos.z}px)`;
			moveHandleFrame(options_pos);
			moveHandle(handle_default_pos);
			hideMasking();
			showHandleFrame();
			hideHandle();
			return;
			// cancelDeploymentOptions();
		}

		hideHandle();
		hideHandleFrame();
		hideMasking();
	}, [deployment_pos, options_pos, operator_key]);

	const border_padding = 200;
	let status = HandleStatus.IDLE;
	let handle_base = null;
	let handle_delta = handle_default_pos;

	const showMasking = () => {
		maskingRef.current.style.opacity = 1;
	};
	const hideMasking = () => {
		maskingRef.current.style.opacity = 0;
	};

	const showHandleFrame = () => {
		wrapperRef.current.style.pointerEvents = 'all';
		handleFrameRef.current.style.pointerEvents = 'all';

		handleFrameRef.current.style.opacity = 1;
	};
	const hideHandleFrame = () => {
		wrapperRef.current.style.pointerEvents = 'none';
		handleFrameRef.current.style.pointerEvents = 'none';

		handleFrameRef.current.style.opacity = 0;
	};
	const showHandle = () => {
		handleRef.current.style.pointerEvents = 'all';
		handleRef.current.style.opacity = 0.6;
	};
	const hideHandle = () => {
		handleRef.current.style.pointerEvents = 'none';
		handleRef.current.style.opacity = 0;
	};

	const moveHandleFrame = ({ x, y, z = 2 }) => {
		handleFrameRef.current.style.transform = `translate3d(${x}px, ${y - 200 * 0.707}px, ${z}px) rotate(45deg)`;
		handleFrameMaskingRef.current.style.transform = `translate(${x}px, ${y - 200 * 0.707 + 3}px) rotate(45deg)`;
	};
	const moveHandle = (data) => {
		const { x , y } = limit(rotate(data));
		handleRef.current.style.transform = `translate3d(${x + 100}px, ${y + 100}px, 0)`;
	};

	const handlePointerDown = (e, data) => {
		if (!operator_key) {
			return;
		}

		status = HandleStatus.PRESSED;
		handle_base = data;

		const handlePointerMove = (e, data) => {
			if (![HandleStatus.PRESSED, HandleStatus.MOVING].includes(status)) {
				return;
			}
			status = HandleStatus.MOVING;
			handle_delta = {
				type: data.type,
				x: data.x - handle_base.x,
				y: data.y - handle_base.y,
			};
			moveHandle(handle_delta);
		};

		const handleMouseMove = e => {
			e.preventDefault();
			handlePointerMove(e, {
				x: e.clientX,
				y: e.clientY,
			});
		};

		const handleTouchMove = e => {
			e.preventDefault();
			handlePointerMove(e, {
				x: e.touches[0].clientX,
				y: e.touches[0].clientY,
			});
		};

		const handlePointerUp = (e) => {
			// Check threshold;
			if (Math.hypot(handle_delta.x, handle_delta.y) > 36){
				const direction = getDirection(handle_delta);
				hideHandleFrame();
				confirmDeployment({
					operator_key,
					...deployment_coord,
					direction,
				});
				// console.log(`Deploymenet confirmed ${direction}`);
			} else {
				cancelDeployment();
				// console.log('Deploymenet canceled');
			}

			status = HandleStatus.IDLE;
			moveHandle(handle_default_pos);
			setTimeout(() => {
				handle_base = null;
			});

			wrapperRef.current && wrapperRef.current.removeEventListener('mousemove', handleMouseMove);
			wrapperRef.current && wrapperRef.current.removeEventListener('touchmove', handleTouchMove);
			window.removeEventListener('mouseup', handlePointerUp);
			window.removeEventListener('touchend', handlePointerUp);
		};

		wrapperRef.current && wrapperRef.current.addEventListener('mousemove', handleMouseMove);
		wrapperRef.current && wrapperRef.current.addEventListener('touchmove', handleTouchMove);
		window.addEventListener('mouseup', handlePointerUp);
		window.addEventListener('touchend', handlePointerUp);
	};

	return (
		<div
			ref={wrapperRef}
			class={style.deployment}
			onClick={e => {
				if (!operator_key || handle_base) {
					cancelDeploymentOptions();
					return;
				}
				// Cancel
				hideHandleFrame();
				cancelDeployment();
			}}
		>
			<svg
				viewBox={`${-border_padding} ${-border_padding} ${masking_size.width + 2 * border_padding} ${masking_size.height + 2 * border_padding}`}
				width={masking_size.width + 2 * border_padding}
				height={masking_size.height + 2 * border_padding}
				class={style.deployment_masking}
				ref={maskingRef}
			>
				<defs>
					<mask id="window_mask">
						<rect
							x={-border_padding}
							y={-border_padding}
							width={masking_size.width + 2 * border_padding}
							height={masking_size.height + 2 * border_padding}
							fill="#fff"
						/>
						<rect
							ref={handleFrameMaskingRef}
							width="200"
							height="200"
							fill="#000"
						/>
					</mask>
				</defs>
				<rect
					style="mask: url('#window_mask')"
					x={-border_padding}
					y={-border_padding}
					width={masking_size.width + 2 * border_padding}
					height={masking_size.height + 2 * border_padding}
					fill="rgba(0, 0, 0, 0.7)"
				/>
			</svg>
			<div
				class={style.handle_frame}
				ref={handleFrameRef}
				onMouseDown={e => {
					// Move handle start
					e.preventDefault();
					e.stopPropagation();
					handlePointerDown(e, {
						x: e.clientX,
						y: e.clientY,
					});
				}}
				onTouchStart={e => {
					e.preventDefault();
					e.stopPropagation();
					handlePointerDown(e, {
						x: e.touches[0].clientX,
						y: e.touches[0].clientY,
					});
				}}
			>
				<div
					class={style.handle}
					ref={handleRef}
				/>
				{
					options_pos && (
						<div
							class={style.withdraw}
							onClick={e => {
								e.preventDefault();
								e.stopPropagation();
								withdrawOperator({
									...options_coord,
									operator_key: options_data.operator.operator_key,
								});
								cancelDeploymentOptions();
							}}
							onTouchEnd={e => {
								e.preventDefault();
								e.stopPropagation();
								withdrawOperator({
									...options_coord,
									operator_key: options_data.operator.operator_key,
								});
								cancelDeploymentOptions();
							}}

						>
							<img src="../../../../../assets/escape.png" alt="withdraw" />
						</div>
					)
				}
				{
					options_pos && options_data.operator.skill_icon && (
						<div
							class={style.skill}
							onClick={e => {
								e.preventDefault();
								console.log('operator skill');
							}}
							onTouchEnd={e => {
								e.preventDefault();
								console.log('operator skill');
							}}
						>
							<ArkSkillIcon skill_key={options_data.operator.skill_icon} size="56px" />
						</div>
					)
				}
			</div>
		</div>
	);
};

export default ArkOperatorDeployment;
