import React from 'preact';
import style from './style';
import available_list from './list';

const ArkProfessionIcon = ({
	profession,
	size,
}) => {
	const url = available_list.includes(profession)
		? `../../assets/profession_icons/${profession}.png`
		: `../../assets/404.png`;

	if (!available_list.includes(profession)) {
		console.log(`Cannot find ${profession}`);
	}

	return (
		<div
			class={style.wrapper}
			style={{ width: size, height: size }}
		>
			<img
				class={style.profession_icon} src={url} alt={`profession_${profession}`}
			/>
		</div>
	);
};

export default ArkProfessionIcon;
