import React from 'preact';
import { useState, useEffect, useRef, useMemo } from 'preact/hooks';
import cn from 'classnames';
import style from './style';

import ArkCharIcon from './../../../../components/charIcon';
import ArkSquashText from '../../../../components/text/squash';
import { ArkError } from '../../../../components/helpBlock';

import ArkMapTile, { style as tileStyle } from './components/tile';
import ArkMapTileLegend from './components/tile/legend';
import getCoordinate from '../../data/getCoordinate';

import ArkMapRoute, { style as routeStyle } from './components/route';
import { clamp } from '../../../../utils';

import ArkOperatorDeployment from './components/deployment';

import ArkWaveEnemy from './components/wave_enemy';

import useDeployment from '../../hooks/useDeployment';
import calculatePosition from '../../data/calculatePosition';

const MultiRoutesRenderOptions = {
	route_index: {
		enabled: true,
		key: 'route_index',
		ref: 'route_index_offset',
		prev_bound: -5,
		next_bound: 0,
	},
	timestamp: {
		enabled: true,
		key: 'timestamp',
		ref: 'current_timestamp',
		prev_bound: -10,
		next_bound: 0,
	},
	disable: {
		enabled: false,
		key: '',
		ref: '',
		prev_bound: 0,
		next_bound: 0,
	},
};

const ArkMap = ({
	env,
	data,
	ir,
	map_object,
	wave_routes,
	squad,
	deployOperator,
	withdrawOperator,
	operator_default_range_tiles,
	operator_active_range_tiles,
	map_operators,
	operator_deployed,
}) => {
	if (!data) {
		return (
			<span>Loading</span>
		);
	}

	const {
		options,
		levelId,
		mapId,
		bgmEvent,
		mapData,
		tilesDisallowToLocate,
		runes,
		globalBuffs,
		routes,
		enemies,
		enemyDbRefs,
		waves,
		branches,
		predefines,
		excludeCharIdList,
		randomSeed,
	} = data;

	const {
		height,
		width,
		tiles,
	} = mapData;

	const presented_tiles = tiles.map(({ tileKey }) => tileKey).reduce((prev, next) => ({
		...prev,
		[next]: true,
	}), {});

	if (height * width !== tiles.length) {
		return (
			<div class={style.map_render}>
				<ArkError message={'Invalid data'} />
			</div>
		);
	}

	const rendererRef = useRef();

	const [show_perspective, setShowPerspective] = useState(true);
	const toggleShowPerspective = () => {
		setShowPerspective(prev =>
			// setDisableHover(!prev);
			 !prev
		);
	};
	const [disable_hover, setDisableHover] = useState(true);
	const toggleDisableHover = () => {
		setDisableHover(prev => !prev);
	};

	const [show_route_order, setShowRouteOrder] = useState(false);
	const [route_index_offset, setRouteIndexOffset] = useState(-1);
	const route_index_max = !show_route_order && wave_routes
		? wave_routes.all_routes_flatten.length - 1
		: routes.filter(Boolean).length - 1;
	const route_index_min = -1;

	const current_timestamp = !show_route_order
		&& wave_routes
		&& route_index_offset >= 0
		&& wave_routes.all_routes_flatten[route_index_offset].timestamp;

	const navRoute = (delta) => {
		setRouteIndexOffset(prev => clamp(prev + delta, route_index_min, route_index_max));
	};
	const goPrevRoute = () => {
		navRoute(-1);
	};
	const goNextRoute = () => {
		navRoute(1);
	};

	const {
		deployment,
		setDeployment,
		confirmDeployment,
		cancelDeployment,
		options: deployment_options,
		setOptions: setDeploymentOptions,
		cancelOptions: cancelDeploymentOptions,
	} = useDeployment({
		deployOperator,
	});

	const [operator_deployment_active, setOperatorDeploymentActive] = useState({});

	const multi_routes = 'timestamp';

	useEffect(() => {
		let d = 0;
		const mouse_handler = (e) => {
			e.preventDefault();
			d += e.deltaY;
			const trigger = Math.abs(d) > 50;
			if (!trigger) return;
			d = 0;
			const up = e.deltaY < 0;
			if (up) {
				goPrevRoute();
			} else {
				goNextRoute();
			}
		};

		const keyboard_handler = (e) => {
			// console.log(e);
			switch (e.key) {
				case 'Home':
					e.preventDefault();
					setRouteIndexOffset(route_index_min);
					break;
				case 'End':
					e.preventDefault();
					setRouteIndexOffset(route_index_max);
					break;
				// case 'ArrowUp':
				// 	goPrevRoute();
				// 	break;
				// case 'ArrowDown':
				// 	goNextRoute();
				// 	break;
				default:
					break;
			}
		};

		rendererRef.current && rendererRef.current.addEventListener('mousewheel', mouse_handler);
		window.addEventListener('keydown', keyboard_handler);

		return () => {
			rendererRef.current && rendererRef.current.removeEventListener('mousewheel', mouse_handler);
			window.removeEventListener('keydown', keyboard_handler);
		};
	}, []);

	useEffect(() => {
		const renderer_width = rendererRef.current && rendererRef.current.clientWidth;
		const renderer_height = rendererRef.current && rendererRef.current.clientHeight;
		const map_width = width * 48 + (width - 1) * 2;
		const map_height = height * 48 + (height - 1) * 2;
		setDeployment({
			transformPosition: ({ row, col, heightType }) => {
				const { x: map_x, y: map_y } = calculatePosition({ row, col, width, height });
				return {
					x: (renderer_width - map_width) / 2 + map_x,
					y: (renderer_height - map_height) / 2 + map_height - map_y,
					z: heightType === 1 ? 24 : 2,
				};
			},
		});
	}, [env.side_panel_opened]);

	useEffect(() => {
		setShowPerspective(!env.default_disable_perspective);
	}, [env]);

	return (
		<div class={style.wrapper}>
			<div
				class={cn(
					'map_render',
					{
						disable_hover,
						map_render_hover: show_perspective,
					}
				)}
				ref={rendererRef}
			>
				<div class={tileStyle.tile_rows}>
					{
						Array(height).fill().map((_, v) => (
							<div class={tileStyle.tile_row}>
								{
									Array(width).fill().map((_, u) => {
										const tile_index = v * width + u;
										const tileData = tiles[tile_index];
										const tile_coord = getCoordinate({ row: v, col: u });
										return (
											<ArkMapTile
												key={`tile-${tile_coord}-${tileData.tileKey}`}
												row={v}
												col={u}
												tile_coord={tile_coord}
												disable_box_animation={env.isMac || env.isMobile}
												operator_default_range={operator_default_range_tiles[tile_index]}
												operator_active_range={
													deployment_options && operator_active_range_tiles[tile_index]
												}
												active_operator={
													deployment_options
														&& deployment_options.options_data
														&& deployment_options.options_data.operator
														&& deployment_options.options_data.operator.operator_key
												}
												setDeployment={e => {
													setOperatorDeploymentActive(prev => ({}));
													if (!map_operators[tile_index]) {
														setDeployment(e);
													}
												}}
												setDeploymentOptions={setDeploymentOptions}
												operator={map_operators[tile_index]}
												{...tileData}
											/>
										);
									})
								}
							</div>
						))
					}
				</div>
				<div class={style.routes_render}>
					<span class={style.route_index}>
						{route_index_offset >= 0 &&
							`Route #${route_index_offset}`
						}
					</span>
					<span class={style.enemy_index}>
						{route_index_offset >= 0 && wave_routes &&
							`${
								wave_routes.all_routes_flatten[route_index_offset].enemy_index_range || 0
							} / ${wave_routes.total_count}`
						}
					</span>
					{
						show_route_order
							? (
								<div class={routeStyle.routes}>
									{
										// In route index order
										routes.filter(Boolean)
											.map((route, index) => (
												route_index_offset >= index - 10
												&& route_index_offset <= index + 20
												&& (
													<ArkMapRoute
														key={`route-${index}`}
														mapData={{
															height,
															width,
														}}
														map_object={map_object}
														route_index={index}
														route_active_refs={{
															route_index_offset,
															current_timestamp,
														}}
														multi_routes_render={MultiRoutesRenderOptions[multi_routes]}
														{...route}
													/>
												)
											))
									}
								</div>
							) : (
								<div class={routeStyle.routes}>
									{
										// In wave order
										wave_routes && wave_routes.all_routes_flatten
											.map(({ routeIndex, enemy_speed, timestamp }, index) => (
												route_index_offset >= index - 10
												&& route_index_offset <= index + 20
												&& (
													<ArkMapRoute
														key={`route-${index}`}
														mapData={{
															height,
															width,
														}}
														map_object={map_object}
														moving_speed={enemy_speed}
														route_index={index}
														timestamp={timestamp}
														route_active_refs={{
															route_index_offset,
															current_timestamp,
														}}
														multi_routes_render={MultiRoutesRenderOptions[multi_routes]}
														{...routes[routeIndex]}
													/>
												)
											))
									}
								</div>
							)
					}
				</div>
				{!env.editorMode && (
					<ArkOperatorDeployment
						squad={squad}
						{...deployment}
						setDeployment={setDeployment}
						confirmDeployment={data => {
							confirmDeployment({
								...data,
								route_index_offset,
							});
						}}
						withdrawOperator={e => {
							withdrawOperator({
								...e,
								route_index_offset,
							});
						}}
						cancelDeployment={cancelDeployment}
						{...deployment_options}
						setDeploymentOptions={setDeploymentOptions}
						cancelDeploymentOptions={cancelDeploymentOptions}
					/>
				)}
				<div class={style.background_text}>
					<ArkSquashText text={ir('map-map-map', '_Map')} fontSize="36px" capitalSize="32px" />
				</div>
			</div>
			{squad && (
				<div class={style.map_squad}>
					<div class={style.map_squad_operators}>
						{
							Object.entries(squad)
								.filter(([operator_key]) => !operator_deployed[operator_key])
								.map(([operator_key, {
									rarity,
									char_key,
									evolve_phase,
									active_skill_index,
								}]) => (
									<div
										class={cn(
											style.map_squad_operator,
											{
												[style.map_squad_operator_active]: operator_deployment_active[operator_key],
											}
										)}
										onClick={e => {
										// Select operator
											setDeployment({
												operator_key,
											});
											setOperatorDeploymentActive(prev => ({
												[operator_key]: true,
											}));
											setDisableHover(true);
										}}
										onMouseDown={e => {
											global.dragDataTransfer = {
												type: 'operator',
												operator_key,
												...squad[operator_key],
											};
											setOperatorDeploymentActive(prev => ({
												[operator_key]: true,
											}));
											setDisableHover(true);
										}}
										onTouchStart={e => {
											e.preventDefault();
											e.stopPropagation();
											global.dragDataTransfer = {
												type: 'operator',
												operator_key,
												...squad[operator_key],
											};
											setOperatorDeploymentActive(prev => ({
												...prev,
												[operator_key]: true,
											}));
											setDisableHover(true);
										}}
									>
										<ArkCharIcon char_key={char_key} rarity={rarity} size="56px" />
									</div>
								))
						}
					</div>
					<div class={style.background_text}>
						<ArkSquashText text={ir('map-map-squad', '_Squad')} fontSize="36px" size="20px" capitalSize="32px" />
					</div>
				</div>
			)}
			{route_index_offset >= 0 && wave_routes && (
				<div class={style.map_enemy}>
					<ArkWaveEnemy
						total_enemy_count={wave_routes.total_count}
						{...wave_routes.all_routes_flatten[route_index_offset]}
					/>
					<div class={style.background_text}>
						<ArkSquashText text={ir('map-map-current_enemy', '_Current _Enemy')} fontSize="36px" size="20px" capitalSize="32px" />
					</div>
				</div>
			)}
			<div class={style.map_legend}>
				<div class={style.background_text}>
					<ArkSquashText text={ir('map-map-legends', '_Legends')} fontSize="36px" capitalSize="32px" />
				</div>
				{
					Object.keys(presented_tiles).map(tileKey => (
						<ArkMapTileLegend ir={ir} tileKey={tileKey} />
					))
				}
			</div>
			<div class={style.map_options}>
				<div class={style.background_text}>
					<ArkSquashText text={ir('map-map-options', '_Options')} fontSize="36px" capitalSize="32px" />
				</div>
				<span
					class={cn(
						'button',
						style.map_option,
						{
							button_active: show_perspective,
						}
					)}
					onClick={() => {
						toggleShowPerspective();
					}}
				>{ir('map-map-options-keep_perspective', 'Keep perspective')}</span>
				<span
					class={cn(
						'button',
						style.map_option,
						{
							button_active: disable_hover,
						}
					)}
					onClick={() => {
						toggleDisableHover();
					}}
				>{ir('map-map-options-disable_interaction', 'Disable interaction')}</span>
			</div>
		</div>
	);
};

export default ArkMap;
