import calculatePosition from './calculatePosition';
import parseCheckpoint from './parseCheckpoint';

const moveToPoint = (last_pos, { x, y }) => {
	let dist = 0;
	if (last_pos.x !== null && last_pos.y !== null) {
		dist = Math.hypot(last_pos.x - x, last_pos.y - y);
	}
	last_pos.x = x;
	last_pos.y = y;
	return dist;
};

const buildRoute = ({
	path, time_duration, time_offset,
}) => ({ style }) => (
	<path
		d={path}
		style={style({ time_offset, time_duration })}
	/>
);

const buildCheckpoint = ({
	x, y, r = 16, time_duration, time_offset, type,
}) => ({ style, get_text }) => (
	get_text
		? (
			<text
				x={x}
				y={y}
				style={style({ time_duration, time_offset })}
				font-size="14px"
				font-weight="normal"
				text-anchor="middle"
				dominant-baseline="central"
			>{time_duration}s</text>
		) : (
			<circle
				cx={x}
				cy={y}
				r={r}
				transform={`rotate(-90 ${x} ${y})`}
				style={style({ time_duration, time_offset, r, type })}
			/>
		)
);

const buildPath = ({
	motionMode,
	viewBoxWidth,
	viewBoxHeight,
	startPosition, // {row, col}
	checkpoints = [], // []
	endPosition, // {row, col}
	spawnRandomRange, // {x, y}
	spawnOffset, // {x, y}
	// provided by Ark-Nights
	map_object,
	enemy_speed,
}) => {
	const route_path_args = [];
	const route_points = [];
	const wait_points = [];

	let points = null;

	if (map_object.initialized && motionMode === 0) {
		points = map_object.findPath({
			startPosition,
			checkpoints,
			endPosition,
			motionMode,
		});
	} else {
		points = [
			startPosition,
			...checkpoints
				.map(parseCheckpoint)
				.filter(Boolean),
			endPosition,
		];
	}

	let total_length = 0;
	let last_point = points[0];
	let last_pos = {
		x: null,
		y: null,
	};

	let disappeared = false;
	points.forEach((point, index) => {
		if (point.type === 5) {
			disappeared = true;
		}
		if (point.type === 6) {
			disappeared = false;
		}

		const { x, y } = calculatePosition(point);
		if ([1, 3, 5].includes(point.type)) {
			// wait
			const { x: xp, y: yp } = calculatePosition(last_point);
			if ([1, 3].includes(point.type)) {
				wait_points.push({
					x: xp,
					y: viewBoxHeight - yp,
					time_duration: point.time,
					current_time: total_length,
					type: disappeared ? 5 : point.type,
				});
			}
			if (index !== 0) return;
		}

		total_length += moveToPoint(last_pos, { x, y });
		route_path_args.push(index === 0 ? 'M' : 'L');
		route_path_args.push(x);
		route_path_args.push(viewBoxHeight - y);
		last_point = point;
	});

	const path = route_path_args.join(' ');
	const route_path = route_points.map((point) => buildRoute(point));
	const checkpoints_path = wait_points.map(({
		current_time,
		...rest
	}) => buildCheckpoint({
		...rest,
		time_offset: current_time / total_length,
	}));

	return {
		path,
		route_path,
		checkpoints_path,
		total_length,
	};
};

export default buildPath;
