import React from 'preact';
import { useState, useEffect } from 'preact/hooks';
import style from './style';
import cn from 'classnames';

import ArkSquashText from './../../components/text/squash';

import ArkMap from './sections/map';
import ArkDeploymentPlan from './sections/plan';
import ArkRoutes from './sections/routes';
import ArkWaves from './sections/waves';
import ArkEnemies from './sections/enemies';
import ArkOptions from './sections/options';
import ArkValuesExplorer from './sections/values';
import ArkOperatorsList from './sections/operators';
import { LEVELS } from '../../models/Levels';

import ArkMapObject from './data/Map';
import getWavesRoutes from './data/getWavesRoutes';

import useMapData from './hooks/useMapData';
import useMapPlan from './hooks/useMapPlan';

const ArkMapEditor = ({
	config,
	data,
	env,
	ir,
	editorMode,
}) => {
	const {
		state: {},
		load,
	} = data;

	const [side_panel_opened, setSidePanelOpen_raw] = useState(true);
	const setSidePanelOpen = (fn) => {
		setSidePanelOpen_raw(fn);
	};

	const [fixture, setFixture] = useState(null);
	const [map_object, setMapObject] = useState(null);
	const [wave_routes, setWaveRoutes] = useState(null);

	const {
		state: mapData, focusData,
		load: loadMapData,
	} = useMapData();

	const {
		state: {
			squad,
			plan,
			operator_default_range_tiles,
			operator_active_range_tiles,
			map_operators,
			operator_deployed,
		},
		load: loadMapPlan,
		initialize: initializeMapPlan,
		deployOperator,
		withdrawOperator,
		applySkill,
		toggleSquadOperator,
		updateSquadOperator,
	} = useMapPlan();

	useEffect(() => {
		load();
		loadMapData();
		loadMapPlan();
		const random_id = ~~(Math.random() * LEVELS.length);
		console.log(random_id);
		const data = LEVELS[random_id];
		setFixture(data);
		console.log(data.levelId);

		initializeMapPlan(data.mapData);

		const mo = new ArkMapObject(data.mapData);
		setMapObject(mo);
		window.mo = mo;

		setWaveRoutes(getWavesRoutes(data));
		// const interval = setInterval(() => {
		// }, 2500);
		// return () => {
		// 	clearInterval(interval);
		// };
	}, []);

	return (
		<div class={'wrapper'}>
			<div class={cn(
				'main_panel',
				{
					side_panel_opened,
				}
			)}
			>
				<div class={style.page}>
					<div class={'section'}>
						<div class={'section_header'}>
							<span>{ir('map-map-section_header', 'Map')}</span>
						</div>

						<div class={cn(
							style.editor_section,
							style.map_section
						)}
						>
							{
							// 	<button onClick={() => {
							// 	const random_id = ~~(Math.random() * LEVELS.length);
							// 	console.log(random_id);
							// 	setFixture(LEVELS[random_id]);
							// }}
							// >Random</button>
							}
							<ArkMap
								env={{
									...env,
									side_panel_opened,
								}}
								ir={ir}
								data={fixture}
								map_object={map_object}
								wave_routes={wave_routes}
								squad={squad}
								deployOperator={deployOperator}
								withdrawOperator={withdrawOperator}
								operator_default_range_tiles={operator_default_range_tiles}
								operator_active_range_tiles={operator_active_range_tiles}
								map_operators={map_operators}
								operator_deployed={operator_deployed}
							/>
						</div>
					</div>
					<div class={'section'} >
						<div class={'section_header'}>
							<span>{ir('map-plan-section_header', 'Deployment Plan')}</span>
						</div>

						<div class={cn(
							style.editor_section,
							style.plan_section
						)}
						>
							<ArkDeploymentPlan data={fixture} squad={squad} plan={plan} />
						</div>
					</div>
					<div
						class={cn(
							'section',
							'section_hidden'
						)}
					>
						<div class={'section_header'}>
							<span>{ir('map-routes-section_header','Routes')}</span>
						</div>

						<div class={cn(
							style.editor_section,
							style.route_section
						)}
						>
							<ArkRoutes data={fixture} />
						</div>
					</div>
					<div class={'section'}>
						<div class={'section_header'}>
							<span>{
								editorMode
									? ir('map-waves-section_header' ,'Waves')
									: ir('map-enemies-section_header' ,'Enemies')
							}</span>
						</div>
						<div class={cn(
							style.editor_section,
							style.enemies_section
						)}
						>
							{editorMode ? (
								<ArkWaves data={fixture} />
							) : (
								<ArkEnemies {...wave_routes} />
							)}
						</div>
					</div>
					<div class={'section'}>
						<div class={'section_header'}>
							<span>{ir('map-options-section_header', 'Options')}</span>
						</div>
						<div class={cn(
							style.editor_section,
							style.options_section
						)}
						>
							<ArkOptions data={fixture} />
						</div>
					</div>
				</div>
			</div>
			<div class={cn(
				'side_panel',
				{
					side_panel_opened,
				}
			)}
			>
				<div class={'side_panel_header'}>
					<span>{ir('side_panel-header', 'Tools')}</span>
					<span
						class={'side_panel_handle'}
						onTouchDown={e => false}
						onClick={e => {
							setSidePanelOpen(value => !value);
						}}
					>Ξ</span>
				</div>
				<div class={'side_panel_body'}>
					<div
						class={cn(
							'side_panel_body_section_header',
							style.operators
						)}
					>
						<ArkSquashText
							text={
								ir('side_panel-operators-squad_formation', '_S_q_u_a_d _F_o_r_m_a_t_i_o_n')
							}
							fontSize="16px"
							size="8px"
						/>
					</div>
					<ArkOperatorsList
						squad={squad}
						ir={ir}
						toggleSquadOperator={toggleSquadOperator}
						updateSquadOperator={updateSquadOperator}
					/>
					{false && (
						<div
							class={cn(
								'side_panel_body_section_header',
								style.tiles
							)}
						><span>Tiles</span></div>
					)}
					{false && (
						<div
							class={cn(
								'side_panel_body_section_header',
								style.tools
							)}
						><span>Tools</span></div>
					)}
					{false && (
						<ArkValuesExplorer data={fixture} />
					)}
				</div>
			</div>
		</div>
	);
};

export default ArkMapEditor;
