import React from 'preact';
import cn from 'classnames';
import { Link } from 'preact-router/match';

const ArkCell = (props) => (
	<div
		class={cn(
			'cell',
			{
				cell_header: props.header,
				cell_fullheight: props.fullheight,
				cell_fullwidth: props.fullwidth,
				cell_halfwidth: props.halfwidth,
				cell_dim: props.dim,
				cell_long_text: props.long_text,
				cell_mobile_long_text: props.mobile_long_text,
				cell_force_no_shrink: props.force_no_shrink,
			}
		)}
		key={props.key}
		style={props.style}
		onClick={e => props.onClick && props.onClick(e)}
	>
		{ props.children || (
			props.href ? (
				<Link href={props.href}>
					<p>{props.content}</p>
				</Link>
			) : (
				<p>{props.content}</p>
			)
		)}
	</div>
);

export default ArkCell;
