import React from 'preact';
import style from './style';

import ArkRow from '../../../../components/row';
import ArkWaveRow from './components/WaveRow';

const ArkWaves = ({
	data,
}) => {
	if (!data) {
		return (
			<span>Loading</span>
		);
	}

	const {
		options,
		levelId,
		mapId,
		bgmEvent,
		mapData,
		tilesDisallowToLocate,
		runes,
		globalBuffs,
		routes,
		enemies,
		enemyDbRefs,
		waves,
		branches,
		predefines,
		excludeCharIdList,
		randomSeed,
	} = data;

	return (
		<div class={style.wrapper}>
			<div class={style.wave_rows}>
				<ArkRow
					cells={[
						{ content: 'wave #' },
						{ content: 'fragment #' },
						{ content: 'action #' },
						{ content: 'enemy' },
						{ content: 'route #' },
						{ content: 'preDelay' },
						{ content: 'count' },
					]}
					header
					style={{ background: '#333' }}
				/>
				{
					waves.filter(Boolean)
						.map((wave, index) => (
							<ArkWaveRow
								key={`wave-${index}`}
								waveIndex={index}
								{...wave}
							/>
						))
				}
			</div>
		</div>
	);
};

export default ArkWaves;
