import React from 'preact';
import style from '../style';

import ArkRow from '../../../../../components/row';
import ArkCell from '../../../../../components/cell';
import ArkCharIcon from '../../../../../components/charIcon';

import getCoordinate from '../../../data/getCoordinate';

const Directions = ['↑', '→', '↓', '←'];
const Ops = ['Deploy', 'Withdraw'];

const ArkPlanRow = ({
	squad,
	row,
	col,
	direction,
	operator_key,
	op,
}) => {
	const char = squad[operator_key];
	if (!char) {
		return;
	}

	const { char_key, rarity } = char;

	const CharIcon = () => (
		<ArkCell>
			<ArkCharIcon char_key={char_key} rarity={rarity} size="56px" />
		</ArkCell>
	);

	return (
		<ArkRow
			cells={[
				CharIcon,
				{ content: getCoordinate({ row, col }) },
				{ content: Directions[direction] },
				{ content: Ops[op] },
			]}
			fullheight
		/>
	);
};

export default ArkPlanRow;
