import React from 'preact';
import style from './style';

import ArkRow from '../../../../components/row';

const ArkOptions = ({
	data,
}) => {
	if (!data) {
		return (
			<span>Loading</span>
		);
	}

	const {
		options,
		levelId,
		mapId,
		bgmEvent,
		mapData,
		tilesDisallowToLocate,
		runes,
		globalBuffs,
		routes,
		enemies,
		enemyDbRefs,
		waves,
		branches,
		predefines,
		excludeCharIdList,
		randomSeed,
	} = data;

	return (
		<div class={style.wrapper}>
			<div class={style.option_rows}>
				<ArkRow
					cells={[
						{ content: 'key', fullwidth: true },
						{ content: 'value', fullwidth: true },
					]}
					header
				/>
				{
					Object.entries(options)
						.map(([option_name, option_value], index) => (
							<ArkRow
								cells={[
									{ content: option_name, fullwidth: true },
									{ content: String(option_value), fullwidth: true },
								]}
							/>
						))
				}
			</div>
		</div>
	);
};

export default ArkOptions;
