import React from 'preact';
import style from '../style';

import ArkWaveFragmentAction from './WaveFragmentAction';

const ArkWaveFragment = ({
	preDelay,
	actions,
	name,
	fragmentIndex,
	waveIndex,
}) => (
	<div
		class={style.fragment_row}
	>
		{
			actions
				.filter(({ actionType }) => actionType === 0)
				.filter(Boolean)
				.map(({
					key,
					...action
				}, index) => (
					<ArkWaveFragmentAction
						key={`wave-${waveIndex}-fragment-${fragmentIndex}-action-${index}`}
						fragmentIndex={fragmentIndex}
						waveIndex={waveIndex}
						actionIndex={index}
						enemyKey={key}
						{...action}
					/>
				))
		}
	</div>
);

export default ArkWaveFragment;
