import React, { Fragment } from 'preact';
import { useState } from 'preact/hooks';
import style from './style';

import ArkCell from '../../../../components/cell';
import ArkRow from '../../../../components/row';
import { ArkError } from '../../../../components/helpBlock';

const processLayer = (data, key) => {
	if (data instanceof Array) {
		return data.filter(Boolean).map(e => processLayer(e, key));
	} else if (data[key] instanceof Array) {
		return data[key];
	}
	return data[key];
};

const QueryRender = (data, query) => {
	const err_render = (<ArkError message={'Invalid query'} />);
	try {
		let result = data;
		const path = query.split('.');
		console.log(data);
		console.log(path);
		path.forEach(key => {
			result = processLayer(result, key);
		});
		console.log(result);

		return result
			.map(
				record => typeof(record) === 'string'
					? `${record}`
					: JSON.stringify(record)
			)
			.map(record => (
				<div class={style.values_query_row}>
					<ArkRow
						cells={[
							{
								content: record,
								fullwidth: true,
							},
						]}
						fullheight
					/>
				</div>
			));
	} catch (err) {
		console.warn(err);
		return err_render;
	}
};

const ArkValuesExplorer = ({
	data,
}) => {
	if (!data) {
		return (
			<span>Loading</span>
		);
	}

	const [query, setQuery] = useState(null);
	return (
		<div class={style.wrapper}>
			<div class={style.values_query}>
				<input type="text" value={query} onChange={e => setQuery(e.target.value)} />
			</div>
			{
				query && QueryRender(data, query)
			}
		</div>
	);
};

export default ArkValuesExplorer;
