import React from 'preact';
import style from './style';
import available_list from './list';

const ArkSkillIcon = ({
	skill_key,
	size,
	enable_hover_detail,
}) => {
	const url = available_list.includes(skill_key)
		? `../../assets/skill_icons/${skill_key}.png`
		: `../../assets/404.png`;

	if (!available_list.includes(skill_key)) {
		console.log(`Cannot find ${skill_key}`);
	}

	return (
		<div class={style.wrapper}>
			<img
				class={style.skill_icon} src={url} alt={skill_key}
				style={{ width: size, height: size }}
			/>
			{
				// TODO: hover key
			}
		</div>
	);
};

export default ArkSkillIcon;
