import React from 'preact';
import style from './style';

const ArkSquashText = ({
	text,
	fontSize = '12px',
	size = '8px',
	capitalSize = '12px',
}) => (
	<div class={style.text_wrapper}>
		{
			typeof(text) === 'string'
				&& [].concat(
					...text.split('_').filter(Boolean).map(
						word => word.split('').map(
							(c, index) => (
								<div class={style.char_holder} style={Object.assign(
									{},
									c === ' ' && {
										width: size,
									},
									index === 0 && {
										overflow: 'visible',
									}
								)}
								>
									<span class={style.char} style={{ fontSize }}>{c}</span>
								</div>
							)
						)
					)
				)
		}
	</div>
);

export default ArkSquashText;
