import React from 'preact';
import style from './style';

import ArkRow from '../../../../components/row';
import ArkPlanRow from './components/PlanRow';

const ArkDeploymentPlan = ({
	plan,
	squad,
	data,
}) => {
	if (!data) {
		return (
			<span>Loading</span>
		);
	}

	const {
		options,
		levelId,
		mapId,
		bgmEvent,
		mapData,
		tilesDisallowToLocate,
		runes,
		globalBuffs,
		routes,
		enemies,
		enemyDbRefs,
		waves,
		branches,
		predefines,
		excludeCharIdList,
		randomSeed,
	} = data;

	return (
		<div class={style.wrapper}>
			<div class={style.route_rows}>
				<ArkRow
					cells={[
						{ content: 'operator' },
						{ content: 'position' },
						{ content: 'direction' },
						{ content: 'operation' },
					]}
					style={{ background: '#333' }}
				/>
				<div class={style.route_rows_scroller}>
					{
						plan
							.map((action, index) => (
								<ArkPlanRow
									squad={squad}
									{...action}
								/>
							))
					}
				</div>
			</div>
		</div>
	);
};

export default ArkDeploymentPlan;
