import React from 'preact';
import style from './style';

import ArkRow from '../../../../components/row';
import ArkEnemyRow from './components/EnemyRow';

const ArkEnemies = ({
	all_routes_flatten,
}) => {
	if (!all_routes_flatten) {
		return (
			<span>Loading</span>
		);
	}

	return (
		<div class={style.wrapper}>
			<div class={style.enemy_rows}>
				<ArkRow
					cells={[
						{ content: 'index' },
						{ content: 'enemy' },
						{ content: 'count' },
						{ content: 'interval' },
						{ content: 'timestamp' },
						{ content: 'wave timestamp' },
					]}
					style={{ background: '#333' }}
				/>
				<div class={style.route_rows_scroller}>
					{
						all_routes_flatten
							.filter(Boolean)
							.map((action, index) => (
								<ArkEnemyRow
									index={index}
									{...action}
								/>
							))
					}
				</div>
			</div>
		</div>
	);
};

export default ArkEnemies;
