import React from 'preact';
import cn from 'classnames';
import style from './style.css';

import ArkEnemyIcon from '../../../../../../components/enemyIcon';
import ArkSquashText from '../../../../../../components/text/squash';

const ArkEnemyLineItem = ({
	label,
	value,
}) => (
	<div class={style.line_item}>
		<span class={style.line_item_label}>{label}: </span>
		<span class={style.line_item_value}>{value}</span>
	</div>
);

const ArkWaveEnemy = ({
	wave_index,
	routeIndex,
	count,
	interval,
	enemy_key,
	timestamp,
	enemy_index_range,
	total_enemy_count,
}) => (
	<div class={style.wrapper}>
		<div class={style.enemy_icon}>
			<ArkEnemyIcon
				enemy_key={enemy_key}
				size="64px"
			/>
		</div>
		<ArkSquashText text="_x" fontSize="20px" />
		<ArkSquashText text={`_${count}`} fontSize="28px" size="16px" />
		<div class={style.wave_info}>
			<ArkEnemyLineItem
				label="Wave"
				value={`#${wave_index}`}
			/>
			<ArkEnemyLineItem
				label="Enemy"
				value={`${enemy_index_range} / ${total_enemy_count}`}
			/>
			<ArkEnemyLineItem
				label="Timestamp"
				value={`${timestamp}s`}
			/>
			{
				count > 1 && (
					<ArkEnemyLineItem
						label="Interval"
						value={interval}
					/>
				)
			}
		</div>
	</div>
);

export default ArkWaveEnemy;
