import React from 'preact';
import cn from 'classnames';
import style from './style';

import { RANGES } from '../../models/Ranges';
import { ArkError } from '../helpBlock';

const getArea = ({ grids }) => {
	const min_row = Math.min(...grids.map(({ row }) => row));
	const min_col = Math.min(...grids.map(({ col }) => col));
	const max_row = Math.max(...grids.map(({ row }) => row));
	const max_col = Math.max(...grids.map(({ col }) => col));

	const area_width = max_col - min_col + 1;
	const area_height = max_row - min_row + 1;

	const tiles = Array(area_height).fill().map((_, v) =>
		Array(area_width).fill().map((_, u) => ({
			row: min_row + v,
			col: min_col + u,
			isCenter: false,
			isCovered: false,
		}))
	);

	grids.forEach(({ row, col }) => {
		const v = row - min_row;
		const u = col - min_col;
		tiles[v][u].isCovered = true;
		if (row === 0 && col === 0) {
			tiles[v][u].isCenter = true;
		}
	});

	return {
		min_row,
		min_col,
		max_row,
		max_col,
		area_height,
		area_width,
		tiles,
	};
};

const ArkRangePreview = ({
	range_key,
	skill_range,
}) => {
	const range_data = RANGES[range_key];
	const {
		tiles,
		area_height,
		area_width,
	} = getArea(range_data);
	if (!range_data) {
		return (
			<div class={style.wrapper}>
				<ArkError message={'Invalid data'} />
			</div>
		);
	}

	const scale = Math.min(3 / area_height, 4 / area_width, 1);

	return (
		<div
			class={cn(
				style.wrapper,
				{
					[style.skill_range]: skill_range,
				}
			)}
		>
			<div
				class={style.range_rows}
				style={{
					transform: `scale(${scale || 1})`,
				}}
			>
				{
					tiles.map(row => (
						<div class={style.range_row}>
							{
								row.map(cell => (
									<div
										class={cn(
											style.range_cell,
											{
												[style.range_cell_covered]: cell.isCovered,
												[style.range_cell_center]: cell.isCenter,
											}
										)}
									/>
								))
							}
						</div>
					))
				}</div>
		</div>
	);
};

export default ArkRangePreview;
