import React, { Fragment } from 'preact';
// import { useState } from 'preact/hooks';
import cn from 'classnames';
import style from './style';

import ArkTileOverlay from './overlay';
import ArkTileOperator from '../operator';

const TileTemplate = (props) => (
	<div
		class={cn(
			style.tile,
			style[props.render_as || props.tileKey],
			{
				[style.tile_block]: props.block,
				[style.tile_box]: props.box,
				[style.tile_fence]: props.fence,
				[style.tile_deployable]: props.deployable,
				[style.tile_disable_box_animation]: props.disable_box_animation,
			}
		)}
		style={{
			zIndex: props.zIndex,
		}}
	>
		{(props.block || props.box || props.fence || props.hole) && (
			<Fragment>
				<span class={style.tile_box_left} />
				<span class={style.tile_box_top} />
				<span class={style.tile_box_right} />
			</Fragment>
		)}
		{props.overlays && props.overlays.map((overlay, index) => (
			<ArkTileOverlay {...overlay} zIndex={props.zIndex + index + 1} />
		))}
		{props.operator && (
			<div class={style.tile_operator}>
				<ArkTileOperator {...props.operator} />
			</div>
		)}
	</div>
);

const TileDict = {
	tile_forbidden: {
		legend: 'Forbidden',
		content: '',
	},
	tile_floor: {
		legend: 'Floor',
		content: '',
	},
	tile_wall: {
		legend: 'Wall',
		content: '',
	},
	tile_road: {
		legend: 'Road',
		content: '',
	},
	tile_start: {
		legend: 'Start',
		content: '',
	},
	tile_flystart: {
		legend: 'Fly Start',
		content: 'air',
		content_on_top: true,
	},
	tile_end: {
		legend: 'End',
		content: '',
	},
	tile_telin: {
		legend: 'Teleport in',
		content: 'in',
	},
	tile_telout: {
		legend: 'Teleport out',
		content: 'out',
	},
	tile_hole: {
		legend: 'Hole',
		content: 'hole',
	},
	tile_empty: {
		legend: 'Empty',
		content: '',
	},
	// special
	tile_grass: {
		legend: 'Grass',
		content: '',
		content_on_top: true,
		effect: 'effect_grass',
	},
	tile_fence: {
		legend: 'Fence',
		content: '',
		content_on_top: true,
	},
	// effects
	tile_healing: {
		legend: 'Healing',
		content: '',
		content_on_top: true,
		effect: 'effect_healing',
	},
	tile_bigforce: {
		legend: 'Big force',
		content: 'F+',
		content_on_top: true,
		effect: 'effect_buff',
	},
	tile_defup: {
		legend: 'Def up',
		content: 'def+',
		content_on_top: true,
		effect: 'effect_buff',
	},
	tile_gazebo: {
		legend: 'Gazebo',
		content: 'air+',
		content_on_top: true,
		effect: 'effect_buff',
	},
	tile_infection: {
		legend: 'Infection',
		content: '',
		effect: 'effect_infection',
	},
	tile_volcano: {
		legend: 'Volcano',
		content: '',
		effect: 'effect_volcano',
	},
	tile_corrosion: {
		legend: 'Corrosion',
		content: '',
		effect: 'effect_corrosion',
	},
	// Tutorial
	tile_rcm_operator: {
		render_as: 'tile_road',
		legend: 'Recommended Operator',
		content: '',
	},
	tile_rcm_crate: {
		render_as: 'tile_road',
		legend: 'Recommended Crate',
		content: '',
	},
	// Box
	tile_crate: {
		legend: 'Box',
		content: '',
	},
};

const ArkMapTile = ({
	blackboard, // null
	buildableType, // 0
	effects, // null
	heightType, // 1
	passableMask, // 2
	tileKey, // "tile_forbidden"
	// Provided by Ark-Nights
	// Coordination info
	row,
	col,
	tile_coord,
	// Range datas
	operator,
	active_operator,
	operator_default_range,
	operator_active_range,
	operator_skill_range,
	// Options
	small,
	disable_box_animation,
	// Deployment
	setDeployment,
	setDeploymentOptions,
}) => {
	const tileInfo = TileDict[tileKey];
	if (!tileInfo) {
		console.error(`${tileKey} is not supported`);
	}

	const is_block = heightType === 1 && tileKey !== 'tile_flystart';
	const is_box = [
		'tile_start',
		'tile_flystart',
		'tile_end',
		'tile_crate',
	].includes(tileKey);
	const is_deployable = buildableType > 0;

	const effect_overlay = tileInfo.effect && {
		type: tileInfo.effect,
		options: {
			content_on_top: is_block,
		},
	};

	const tile_content_overlay = tileInfo.content && {
		type: 'content',
		content: tileInfo.content,
		options: {
			content_on_top: is_block && tileInfo.content_on_top,
			content_on_box: is_box && tileInfo.content_on_top,
			content_fade_out_perspective: ['tile_hole'].includes(tileKey),
		},
	};

	const show_tile_coord = ![
		'tile_hole',
		'tile_empty',
	].includes(tileKey) && !(!is_block && tile_content_overlay);

	const tile_coord_overlay = show_tile_coord && tile_coord && {
		type: 'coord',
		content: tile_coord,
		options: {
			content_fade_out_perspective: is_block,
			content_fade_out_flat: tileInfo.content_on_top,
		},
	};

	const operator_default_range_overlays = operator_default_range && Object.entries(operator_default_range).map(
		([operator_key, options]) => ({
			type: 'operator_default_range',
			options: {
				...options,
				content_on_top: is_block,
				range_active: active_operator === operator_key,
			},
		})
	);

	// const operator_active_range_overlay = operator_active_range && {
	// 	type: 'operator_active_range',
	// 	options: {
	// 		content_on_top: is_block,
	// 		is_healing: operator_active_range.is_healing,
	// 	},
	// };

	const operator_skill_range_overlay = operator_skill_range && {
		type: 'operator_skill_range',
		options: {
			content_on_top: is_block,
			is_healing: operator_skill_range.is_healing,
		},
	};

	const deployable_overlay = is_deployable && {
		type: 'deployable',
		options: {
			content_on_top: is_block,
		},
	};

	const overlays = [].concat(
		effect_overlay,
		tile_coord_overlay,
		tile_content_overlay,
		operator_default_range_overlays,
		// operator_active_range_overlay,
		operator_skill_range_overlay,
		deployable_overlay,
	)
		.filter(Boolean)
		.map(o => ({
			options: {},
			...o,
		}));

	if (small) {
		return (
			<div class={cn(
				style.tile_wrapper,
				style.small
			)}
			>
				<TileTemplate
					{...tileInfo}
					tileKey={tileKey}
					block={is_block}
					hole={['tile_hole'].includes(tileKey)}
					deployable={[
						'tile_wall',
						'tile_road',
						'tile_healing',
						'tile_grass',
						'tile_gazebo',
						'tile_defup',
						'tile_bigforce',
					].includes(tileKey)}
					overlays={[
						effect_overlay,
						tile_content_overlay,
					].filter(Boolean)}
				/>
			</div>
		);
	}

	return (
		<div
			class={style.tile_wrapper}
			onClick={e => {
				setDeployment({
					deployment_coord: { row, col, heightType },
					tile_buildable_type: buildableType,
				});

				if (operator) {
					setDeploymentOptions({
						options_coord: { row, col, heightType },
						options_data: { operator },
					});
				}
			}}
			onMouseUp={e => {
				if (global.dragDataTransfer && global.dragDataTransfer.type === 'operator') {
					// Show direction square
					setDeployment({
						operator_key: global.dragDataTransfer.operator_key,
						deployment_coord: { row, col, heightType },
						tile_buildable_type: buildableType,
					});
					delete global.dragDataTransfer;
				}
			}}
			onTouchEnd={e => {
				if (global.dragDataTransfer && global.dragDataTransfer.type === 'operator') {
					// Show direction square
					setDeployment({
						operator_key: global.dragDataTransfer.operator_key,
						deployment_coord: { row, col, heightType },
						tile_buildable_type: buildableType,
					});
					delete global.dragDataTransfer;
				}
			}}
		>
			<TileTemplate
				{...tileInfo}
				tileKey={tileKey}
				block={is_block}
				box={is_box}
				deployable={is_deployable}
				fence={['tile_fence'].includes(tileKey)}
				hole={['tile_hole'].includes(tileKey)}
				operator={operator}
				overlays={overlays}
				zIndex={row * -10 + 5900}
				disable_box_animation={disable_box_animation}
			/>
		</div>
	);
};

export {
	style,
	TileDict,
};

export default ArkMapTile;
