import React from 'preact';
import ArkRow from '../../../../../components/row';

import ArkCell from '../../../../../components/cell';
import ArkEnemyIcon from '../../../../../components/enemyIcon';

const EnemyIconCell = (enemy_key, size) => (props) => (
	<ArkCell>
		<ArkEnemyIcon
			{...props}
			enemy_key={enemy_key}
			size={size}
		/>
	</ArkCell>
);

const ArkEnemyRow = ({
	index,
	routeIndex,
	wave_index,
	fragment_index,
	action_index,
	count,
	enemy_key,
	interval,
	wave_timestamp,
	timestamp,
	total_count,
	wait,
	enemy_index_range,
}) => (
	<ArkRow
		cells={[
			{ content: index },
			EnemyIconCell(enemy_key, '48px'),
			{ content: count },
			{ content: interval },
			{ content: Math.round(timestamp * 100) / 100 },
			{ content: Math.round(wave_timestamp * 100) / 100 },
			{ content: wait && (wait === -1 ? 'Can wait' : `Max ${wait}s`) },
		]}
		fullheight
	/>
);

export default ArkEnemyRow;
