const calculatePosition = ({
	width, // map
	height, // map
	row,
	col,
	reachOffset,
	tile_size = 48,
	spacing = 2,
}) => {
	const x_base = tile_size / 2;
	const y_base = tile_size / 2;

	let x = x_base + col * (tile_size + spacing);
	let y = y_base + row * (tile_size + spacing);

	if (reachOffset) {
		x += reachOffset.x * (tile_size + spacing);
		y += reachOffset.y * (tile_size + spacing);
	}

	return {
		x, y,
	};
};

export default calculatePosition;
