import { useState } from 'preact/hooks';

const default_deployment = {
	operator_key: null,
	target_pos: null,
	transformPosition: d => d,
};

const default_options = {
	options_pos: null,
	options_coord: null,
	options_data: {},
};

const useDeployment = ({
	deployOperator,
}) => {
	const [deployment, setDeployment_raw] = useState(default_deployment);
	const [options, setOptions_raw] = useState(default_options);

	const setDeployment = (data) => {
		if (data.transformPosition) {
			setDeployment_raw(prev => ({
				...prev,
				...data,
			}));
			return;
		}
		setDeployment_raw(prev => {
			if (!prev.operator_key && !data.operator_key) {
				return prev;
			}
			const deployment_pos = prev.transformPosition && data.deployment_coord
				&& prev.transformPosition(data.deployment_coord);
			return ({
				...prev,
				...data,
				deployment_pos,
			});
		});
	};

	const setOptions = (data) => {
		setOptions_raw(prev => {
			const options_pos = deployment.transformPosition && data.options_coord
				&& deployment.transformPosition(data.options_coord);
			return ({
				...prev,
				...data,
				options_pos,
			});
		});
	};

	const cancelOptions = () => {
		setOptions_raw(() => default_options);
	};

	const confirmDeployment = (data) => {
		deployOperator(data);
		clearDeployment();
	};

	const cancelDeployment = (data) => {
		clearDeployment();
	};

	const clearDeployment = () => {
		setDeployment_raw(({
			transformPosition,
		}) => ({
			...default_deployment,
			transformPosition,
		}));
	};

	return {
		deployment,
		setDeployment,
		confirmDeployment,
		cancelDeployment,
		options,
		setOptions,
		cancelOptions,
	};
};

export default useDeployment;
