import { useReducer } from 'preact/hooks';

export const STORAGE_KEY =  'Towa_ArkMap_Editor';

const default_state = {
	map: {},
};

const save = (key, state) => {
	const data = {
		...state,
	};
	window.localStorage.setItem(key, JSON.stringify({
		...data,
	}));
};

const reducer = (state, action) => {
	let newState = state;
	switch (action.type) {
		case 'data.load': {
			const json = action.payload || window.localStorage.getItem(STORAGE_KEY);
			try {
				if (json) {
					const loaded = JSON.parse(json);
					if (action.payload) {
						save(STORAGE_KEY, loaded);
					}

					return {
						...loaded,
					};
				}
			} catch (err) {
				if (action.payload) {
					throw err;
				}
				window.localStorage.removeItem(STORAGE_KEY);
				return default_state;
			}
			break;
		}
		default:
			throw new Error(`Undefined action type: ${action.type}`);
	}

	if (action.type !== 'data.load') {
		save(STORAGE_KEY, newState);
	}
	return newState;
};

const useData = () => {
	const [state, dispatch] = useReducer(reducer, default_state);

	const load = (json) => dispatch({
		type: 'data.load',
		payload: json,
	});

	return {
		state,
		dispatch,
		load,
	};
};

export default useData;
