import React from 'preact';
import style from './style';
import available_list from './list';

const ArkEliteIcon = ({
	evolve_phase,
	size,
}) => {
	const url = available_list.includes(evolve_phase)
		? `../../assets/elite_icons/${evolve_phase}.png`
		: `../../assets/404.png`;

	if (!available_list.includes(evolve_phase)) {
		console.log(`Cannot find ${evolve_phase}`);
	}

	return (
		<div
			class={style.wrapper}
			style={{ width: size, height: size }}
		>
			<img
				class={style.elite_icon} src={url} alt={`elite_phase_${evolve_phase}`}
			/>
		</div>
	);
};

export default ArkEliteIcon;
