import React from 'preact';
import style from './style';

import ArkRow from '../../../../components/row';
import ArkRouteRow from './components/RouteRow';

const ArkRoutes = ({
	data,
}) => {
	if (!data) {
		return (
			<span>Loading</span>
		);
	}

	const {
		options,
		levelId,
		mapId,
		bgmEvent,
		mapData,
		tilesDisallowToLocate,
		runes,
		globalBuffs,
		routes,
		enemies,
		enemyDbRefs,
		waves,
		branches,
		predefines,
		excludeCharIdList,
		randomSeed,
	} = data;

	return (
		<div class={style.wrapper}>
			<div class={style.route_rows}>
				<ArkRow
					cells={[
						{ content: 'index' },
						{ content: 'mode' },
						{ content: 'start' },
						{ content: 'end' },
					]}
					style={{ background: '#333' }}
				/>
				<div class={style.route_rows_scroller}>
					{
						routes
							.filter(Boolean)
							.map((route, index) => (
								<ArkRouteRow
									routeIndex={index}
									{...route}
								/>
							))
					}
				</div>
			</div>
		</div>
	);
};

export default ArkRoutes;
