import React from 'preact';
import style from '../style';

import ArkRow from '../../../../../components/row';
import ArkWaveFragment from './WaveFragment';

const ArkWaveRow = ({
	preDelay,
	postDelay,
	maxTimeWaitingForNextWave,
	fragments,
	name,
	waveIndex,
}) => {
	// console.log(fragments);
	return (
		<div
			class={style.fragment_rows}
		>
			{
				fragments.filter(Boolean)
					.map((fragment, index) => (
						<ArkWaveFragment
							key={`wave-${waveIndex}-fragment-${index}`}
							waveIndex={waveIndex}
							fragmentIndex={index}
							{...fragment}
						/>
					))
			}
		</div>
	);
};

export default ArkWaveRow;
